                input: "#FFFFFF",
                email: "#494848",
                text: "#FFFFFF",
            }
        };
    },
    methods: {
        startClaim() {
            this.isClickClaim = true;
            this.claim.message = {
                type: '',
                text: '',
            }
        },
        async claimNode(data) {
            return new Promise((resolve) => {
                this.isConnecting = false;
                ethersHelper.switchNetwork().then(() => {
                    const address = data.metaMaskAddress;
                    const chainId = data.netID;
                    if ((address !== null && chainId !== null) && (Number(chainId) !== Number(ethersHelper.masterNetId()))) {
                        this.claim.message = {
                            type: "error",
                            text: `请将小狐狸钱包切换到以太坊主网`,
                        }
                        this.isClickClaim = false;
                        resolve();
                    } else {
                        if (address === null || address === undefined || address === "") {
                            this.claim.message = {
                                type: "error",
                                text: "请接入您的钱包, 再点击一次",
                            };
                            this.isClickClaim = false;
                            resolve();
                        } else {
                            const isInWhitelist = checkIsInWhitelist(address);
                            if (isInWhitelist) {
                                isAlreadyMinted(address).then((isMined) => {
                                    if (!isMined) {
                                        mintWhitelist(address)
                                            .then((tx) => {
                                                this.claim.message = {
                                                    type: "info",
                                                    duration: 300000,
                                                    text: "正在鑄造中... (当前网络可能拥塞，可透过钱包确认结果)",
                                                };
                                                tx.wait()
                                                    .then((ret) => {
                                                        this.claim.message = {
                                                            type: "success",
                                                            duration: 100000,
                                                            text: "领取身份成功! 别忘记设置会议提醒",
                                                        };
                                                        this.isClickClaim = false;
                                                        resolve(ret);
                                                    })
                                                    .catch((error) => {
                                                        this.claim.message = {
                                                            type: "error",
                                                            text: `抱歉，鑄造失敗, 错误讯息: ${error}`,
                                                        };
                                                        this.isClickClaim = false;
                                                        resolve(error);
                                                    });
                                            })
                                            .catch((error) => {
                                                this.claim.message = {
                                                    type: "error",
                                                    text: `抱歉，请再尝试一次, 错误讯息: [mintWhitelist] ${error}`,
                                                };
                                                this.isClickClaim = false;
                                                resolve(error);
                                            });
                                    } else {
                                        this.claim.message = {
                                            type: "success",
                                            text: `你已经 Claim 成功了，请查看自己的以太坊钱包`,
                                        };
                                        this.isClickClaim = false;
                                        resolve();
                                    }
                                }).catch((error) => {
                                    this.claim.message = {
                                        type: "error",
                                        text: `抱歉，请再尝试一次, 错误讯息: [isAlreadyMinted] ${error}`,
                                    };
                                    this.isClickClaim = false;
                                    resolve(error);
                                });
                            } else {
                                this.claim.message = {
                                    type: "error",
                                    duration: 10000,
                                    text: `抱歉，您不在白名单中~ (当前錢包: ${address.slice(0, 5)}...${address.slice(address.length - 5, address.length)})`,
                                };
                                this.isClickClaim = false;
                                resolve();
                            }
                        }
                    }
                }).catch((error) => {
                    console.error(error)
                    this.claim.message = {
                        type: "error",
                        text: `请将小狐狸钱包切换到以太坊主网`,
                    }
                    this.isClickClaim = false;
                    resolve(error);
                });
            });
        },
        connectWallet() {
            this.isConneting = true;
        },
        async onComplete(data) {
            console.log('data:', data);
            await this.switchNetwork()
        },
        countDownS_cb: function (x) {
            console.log(x)
        },
        countDownE_cb: function (x) {
            console.log(x)
        },
        timerCount: function (start, end) {
            // Get todays date and time
            var now = new Date().getTime();

            // Find the distance between now an the count down date
            var distance = start - now;
            var passTime = end - now;

            if (distance < 0 && passTime < 0) {
                clearInterval(this.interval);
                return;
            } else if (distance < 0 && passTime > 0) {
                this.calcTime(passTime);
            } else if (distance > 0 && passTime > 0) {
                this.calcTime(distance);
            }
        },
        calcTime: function (dist) {
            // Time calculations for days, hours, minutes and seconds
            this.days = Math.floor(dist / (1000 * 60 * 60 * 24));
            this.hours = Math.floor(
                (dist % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
            );
            this.minutes = Math.floor((dist % (1000 * 60 * 60)) / (1000 * 60));
            this.seconds = Math.floor((dist % (1000 * 60)) / 1000);
        },
    }
}
</script>

